import React from 'react'
// import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';


const Navbar = ({ icon, title }) => {


    return (
        <nav className='navbar bg-success'>
            <h1>
                <i className={icon} />
                {title}
            </h1>
            <ul>
                <li>
                    <Link to='/'>Home</Link>
                </li>
                <li>
                    <Link to="/about">About</Link>
                </li>
            </ul>
        </nav>
    )

}
Navbar.defaultProps = {
    title: 'SEARCH Github',
    icon: 'fab fa-github'
}
export default Navbar;
